.App {
  overflow: hidden;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  display: flex;
  flex-direction: column;
  
}

.container{
  display: flex;
  align-content: center;
  
}
.container-item{

}


.video-background{
  position: absolute;
  display: block;
  margin: auto;
  width: 100%;
  clip-path: polygon(0% 0%, 0% 100%, 25% 100%, 25% 17%, 75% 17%, 75% 33%, 25% 33%, 18% 100%, 100% 100%, 100% 0%);

  overflow: hidden;
  z-index: -2;
}
.image-background{
  background-image: url("./Assets/backgroundnotvid.jpg");
  background-repeat: no-repeat;
  width: 100%;
  background-position: center;
  z-index: -3;
  
}
.hero-medium{
  margin:auto;
  padding-top: 18vh;
  padding-bottom: 20vh;
  text-align: center;
  background-color: hsla(0, 0%, 0%, 0.50);
  width:100%;
}
.title{
  color: white;
  letter-spacing: .1em;
  font-size: 4vh;
  padding-bottom: 0vh;
  margin-bottom: 0vh;
  text-shadow: .01em .01em .1em white;
}



.navbar-menu{
  position: fixed;
  top: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  width: 100%;
  box-shadow: .2vh .2vh 1vh black;
  background: #272538;
  border-bottom: 3px solid #988cff;
}
.navbar-item{
  align-self: flex-end;
  display: block;

  text-decoration: none;
  color: white;
  font-size: 2.2vh;
  font-weight: bolder;

  padding: 2vh 1vh 2vh;
  
}
.navbar-item:hover{
  background:  linear-gradient(to bottom, #a0fff255, #00000055);
  
  filter: drop-shadow(.2vh .2vh 1vh white);
}
.navbar-item-brand{
  padding: 1px;
  background:  linear-gradient(to bottom, #f3f1ff55, #00000055);
  
  filter: drop-shadow(.2vh .2vh 1vh white);
}

.navbar-item-spacer{
  flex-grow: 10;
}



.content-styling{

  display: flex;
  flex-direction: column;
  height: 100%;

  
  background-image: linear-gradient(to left top, #272538, #000000);

  
}
.content-section{
display: flex;
justify-content: space-evenly;
align-items: center;
}
.content-title{
  text-align: center;
  color: white;
  letter-spacing: .2em;
  padding-bottom: 1vh;
  margin-left: 8vh;
  font-size: 10vh;
  border-bottom: 2 px solid white;
}
.content-left{
  width: 20%;
  align-self: center;
  margin-left: 20vh;
  margin-bottom: 5vh;
  font-size: 1.3em;
  color: white;
  line-height: 1.5em;


}
.content-image{
  width: 100%;
  clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%);
}
.content-right{
  width: 50%;
  margin-bottom: 5vh;
  margin-left: 22vh;
  font-size: 1.3em;
  color: white;
  line-height: 1.8em;
  align-self: center;
}

.content-card{
  padding: 3vh;
  display: flex;
  flex-direction: column;
  align-content: space-around;
  justify-content: center;  
  font-size: 1.3em;
  color: white;
  line-height: 1.8em;
}

.content-img{
  height: 10vh;
}

ul{
  list-style-type: none;
}

.shadow{
  filter: drop-shadow(.2vh .2vh 1vh grey);
}
.text-glow{
  text-shadow: .01em .01em .1em white, -.05em .01em .1em pink ;
  
}
